//Full Page Overlay Dark Tint
@mixin full-page-overlay {
    content: '';
    background-color: black;

    position: absolute;

    width: 100%;
    height: 100%;
    // When overlay is hidden I want to be able to click on links so z-index helps
    opacity: var(--overlay_opacity, 0);
    z-index: var(--overlay_z-index, -2);
    
    transition: opacity ease-in 200ms, z-index ease-in 200ms;
}